.App {
  min-height: 100vh;
  background-image: linear-gradient(to right, #f83600 0%, #f9d423 100%);
}

.search-form {
min-height: 10vh;
display: flex;
justify-content: center;
align-items: center;
}

.search-bar {
  width: 50%;
  border: none;
  padding: 10px;
}

.search-button {
  background: #f83600;
  border: 2px;
  padding: 10px 20px;
  color: white;
}

.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}